ii hyvin yhteen monen kanssa, esim. grillattujen kasvisten, uunipaahdetun bataatin, kanan ja vaikkapa kuhan kanssa.</li></ol></div><div class=\"right\" data-v-b2c70aca><img"+(_vm._ssrAttr("src",require("assets/img/recipes/harissa.jpg")))+" data-v-b2c70aca></div></article>")])}
